import axios from "axios";

const state = {
  topics: [],
  selectedTopic: null
};
const getters = {
  getTopics: state => state.topics,
  getSelectedTopic: state => state.selectedTopic
};
const mutations = {
  SELECT_TOPIC: (state, topic) => {
    state.selectedTopic = topic;
  },
  SET_TOPICS: (state, topics) => {
    state.topics = topics;
  }
};
const actions = {
  fetchTopics({ commit }) {
    axios
      .get("https://us-central1-flash-cards-799a5.cloudfunctions.net/topics")
      .then(r => {
        return r.data;
      })
      .then(topics => {
        commit("SET_TOPICS", topics);
      });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
