<template>
  <div class="topics-list flex flex-wrap">
    <div
      v-for="topic in getTopics"
      :key="topic.id"
      class="flex items-center justify-center border border-dashed my-4 mx-3 rounded-lg text-center"
      :style="{ borderColor: getRandomBorderColor() }"
    >
      <input type="radio" name="topicCard" :id="topic.id" :value="topic.id" />
      <label :for="topic.id" @click="selectCard(topic)">
        {{ topic.name }}
      </label>
    </div>
  </div>
</template>

<script>
import store from "../store";
import { mapGetters } from "vuex";

export default {
  name: "TopicCardList",
  props: ["topics"],
  mounted() {
    this.$store.dispatch("fetchTopics");
  },
  data() {
    return {
      borderColors: [
        "#F65B39",
        "#BA39F6",
        "#35BFD1",
        "#F67239",
        "#5BA649",
        "#3941F6",
        "#BA39F6"
      ]
    };
  },
  methods: {
    selectCard(topic) {
      store.commit("SELECT_TOPIC", topic);
    }
  },
  computed: {
    ...mapGetters(["getTopics"]),
    getRandomBorderColor() {
      return () =>
        this.borderColors[Math.floor(Math.random() * this.borderColors.length)];
    }
  }
};
</script>

<style scoped>
.topics-list {
  @apply inline-flex m-2;
}

.topics-list input[type="radio"] {
  @apply fixed opacity-0 w-0;
}

.topics-list label {
  @apply inline-block;

  min-width: 142px;
  min-height: 80px;
  vertical-align: middle;
}

.topics-list label:hover {
  @apply bg-theme-accent;
}

.topics-list input[type="radio"]:focus + label {
  @apply border-theme-accent;
}

.topics-list input[type="radio"]:checked + label {
  @apply bg-theme-accent;
}
</style>
