import Vue from "vue";
import Vuex from "vuex";
import VueAxios from "axios-vue";

import topics from "./modules/topics";

Vue.use(Vuex);
Vue.use(VueAxios);

const state = {
  theme: "theme-light"
};
const store = new Vuex.Store({
  state: {
    ...state
  },
  getters: {
    getTheme: state => {
      if (!state || !state.theme) {
        return "theme-light";
      }
      return state.theme;
    }
  },
  mutations: {
    TOGGLE_THEME: state => {
      state.theme =
        state.theme === "theme-light" ? "theme-dark" : "theme-light";
    }
  },
  actions: {
    toggleTheme: ({ commit }) => {
      commit("TOGGLE_THEME", {});
    }
  },
  modules: {
    topics: topics
  }
});

export default store;
